<template>
  <v-container>
    <div v-if="loading" class="loading">Loading data...</div>
      <v-table theme="dark" width="100%">
        <thead>
          <tr>
            <th class="text-left">Id</th>
            <th class="text-left">Title</th>
            <th class="text-left">Author</th>
            <th class="text-left">Price</th>
            <th class="text-center">Image</th>
            <th class="text-center btn-column">Action</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="book in bookList" :key="book.id">
            <td class="text-left">{{ book.id }}</td>
            <td class="text-left">{{ book.title }}</td>
            <td class="text-left">{{ book.author }}</td>
            <td class="text-left">{{ book.price }}</td>
            <td class="text-left"><v-img :src="book.imageUrl" class="img"/></td>
            <td>
                  <v-row justify="space-around">
                      <v-btn
                      color="info"
                      @click="showUpdateBookDialog(book)"
                      >Update</v-btn
                      >
                      <v-btn
                      class="mr-3"
                      color="error"
                      @click="deleteBook(book)"
                      >Delete</v-btn
                      >
                  </v-row>
            </td>
          </tr>
        </tbody>
      </v-table>
        <v-row justify-start class="mt-4">
          <v-col cols="4" class="d-flex justify-start">
            <v-btn flat color="info" @click="showCreateBookDialog">Add book</v-btn>
          </v-col>
        </v-row>
      <v-dialog v-model="showDialog" max-width="500px" min-height="450px">
        <v-card>
          <v-card-title class="pl-7">{{ dialogTitle }}</v-card-title>
          <v-card-text>
            <v-form>
              <v-text-field v-model="title" label="Title" placeholder="Title..."></v-text-field>
              <v-text-field v-model="author" label="Author" placeholder="Author..."></v-text-field>
              <v-text-field v-model="price" label="Price" placeholder="0.00"></v-text-field>
              <v-text-field v-model="imageUrl" label="Image url" placeholder="Image url"></v-text-field>
              <v-row justify="space-between">
                <v-col cols="4">
                  <v-btn v-if="updateBookButton" flat color="info" @click="submitUpdate">Update</v-btn>
                  <v-btn v-if="!updateBookButton" flat color="info" @click="submitCreateBook">Create book</v-btn>
                </v-col>
                <v-col cols="4"><v-btn color="error" block @click="showDialog = false">Close Dialog</v-btn></v-col>    
              </v-row>
            </v-form>
          </v-card-text>
        </v-card>
      </v-dialog>
  </v-container>
</template>

<script setup>
import { computed, onMounted, ref } from 'vue';
import{ getAllBooks, deleteBookById, updateBookById, createBook } from './service.js';

const bookList = ref([]);
const loading = ref(true);
const showDialog = ref(false);
const bookId = ref(0);
const title = ref('');
const author = ref('');
const price = ref(null);
const imageUrl = ref('');
const dialogTitle = ref('Update book data');
const updateBookButton = ref(true);

const fetchData = async () => {
  try {
    //const response = await getAllBooks();
    bookList.value = await getAllBooks();
  } catch (error) {
    console.log('Error: ', error);
  } finally {
    loading.value = false;
  }
}

const showCreateBookDialog = (book) => {
  // reset polja
  resetDalogFields();
  updateBookButton.value = false;
  dialogTitle.value = 'Insert book data';
  showDialog.value = true;
}

const submitCreateBook = async () => {
  await createBook({
    title: title.value,
    author: author.value,
    price: price.value,
    imageUrl: imageUrl.value
  });
  showDialog.value = false;
  await fetchData();

  // reset polja
  resetDalogFields();
}

const showUpdateBookDialog = (book) => {
  bookId.value = book.id;
  title.value = book.title;
  author.value = book.author;
  price.value = book.price;
  imageUrl.value = book.imageUrl;

  updateBookButton.value = true;
  dialogTitle.value = 'Update book';
  showDialog.value = true;
}

const submitUpdate = async () => {
  await updateBookById(bookId.value, {
    title: title.value,
    author: author.value,
    price: price.value,
    imageUrl: imageUrl.value
  });
  showDialog.value = false;
  await fetchData();

  // reset polja
  resetDalogFields();
}

const deleteBook = async (book) => {
  await deleteBookById(book.id);
  await fetchData();
}

const resetDalogFields = () => {
  bookId.value = 0;
  title.value = '';
  author.value = '';
  price.value = '';
  imageUrl.value = '';
}

onMounted(fetchData);
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.img {
  width: 100px;
  margin: auto;
}

.btn-column {
  width: 240px !important;
 }
</style>
