import axios, { AxiosRequestConfig } from "axios";

const BASE_URL = "http://localhost:8080/api/bookstore";

async function GET(path) {
    try {
        const response = await axios.get(BASE_URL + path);
        if(response.data) return response.data;
    } catch (error) {
        console.log('error: ', error);
    }
}

async function DELETE(path) {
    try {
        const axiosConfig = {
            method: "DELETE",
            url: BASE_URL + path
        };

        const response = await axios(axiosConfig);
        if(response.status === 204) return response.status;
    } catch (error) {
        throw new Error(error);
    }
}

async function PUT(path, data) {
    try {
        const jsonData = JSON.stringify(data);
        const axiosConfig = {
            method: "PUT",
            url: BASE_URL + path,
            headers: {
                "Content-Type": "application/json",
            },
            data: jsonData
        }
        const response = await axios(axiosConfig);
        if(response.status === 200 && response.data) return response.data;
    } catch (error) {
        throw new Error(error);
    }
}

async function POST(path, data) {
    try {
        const jsonData = JSON.stringify(data);
        const axiosConfig = {
            method: "POST",
            url: BASE_URL + path,
            headers: {
                "Content-Type": "application/json",
            },
            data: jsonData
        }

        const response = await axios(axiosConfig);
        if(response.status === 201 && response.data) return response.data;
    } catch (error) {
        throw new Error(error);
    }
}

const api = { GET, DELETE, PUT, POST }

export default api;